:root {
  --color-primary: #0095ff;
  --color-primary-light: #8ecefd;
  --color-primary-dark: #0664a7;
  --main-color: #121212;
  --main-color-2: #2a2a2a;
  --color-Grey-light-1: #faf9f9;
  --color-Grey-light-2: #f4f2f2;
  --color-Grey-light-3: #f0eeee;
  --color-Grey-light-4: #ccc;
  --color-Grey-dark-1: #222;
  --color-Grey-dark-2: #777;
  --color-Grey-dark-3: #999;
  --u: 1.5vmin;
  --clr: #777;
  --bg: #444857;
  --neon-light: 0 1.2rem 2rem var(--color-primary-dark);
  --shadow-dark: 1rem 2rem 6rem rgba(black, .6) !important;
}

a {
  text-decoration: none;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  font-size: 62.5%;
}

@media only screen and (width <= 75em) {
  html {
    font-size: 56.25%;
  }
}

@media only screen and (width <= 56.25em) {
  html {
    font-size: 50%;
  }
}

@media only screen and (width >= 112.5em) {
  html {
    font-size: 75%;
  }
}

body {
  background-color: var(--main-color);
  font-family: Poppins, sans-serif;
}

::-webkit-scrollbar {
  width: .5rem;
  height: .8rem;
  transition: all .3s;
}

body::-webkit-scrollbar-thumb {
  background-color: var(--color-primary);
  border-radius: 13rem;
  transition: all .3s;
}

body::-webkit-scrollbar-thumb:active {
  background-color: var(--color-primary-dark);
}

::-webkit-scrollbar-thumb:horizontal {
  background-color: var(--color-primary-light);
  border-radius: 13rem;
  height: .4rem;
  transition: all .3s;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--color-primary-dark);
}

*, :after, :before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

section {
  opacity: 0;
  transition: all 1.5s;
  transform: translateY(-1rem);
}

.section-active {
  opacity: 1 !important;
  transform: translateY(0)translateX(0) !important;
}

a {
  -webkit-user-select: none;
  -webkit-touch-callout: none;
}

::selection {
  color: #fff;
  background-color: #8accfc;
}

@keyframes loading {
  0% {
    width: 0;
  }

  100% {
    width: 100%;
  }
}

@keyframes appear {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes up {
  0% {
    opacity: 0;
    transform: translateY(-1rem);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateX(0);
  }

  40% {
    transform: translateX(-2rem);
  }

  60% {
    transform: translateX(-1rem);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.hidden {
  opacity: 0;
  transition: all .3s;
  z-index: 0 !important;
}

.slided {
  opacity: 1 !important;
}

.hovered {
  opacity: 1;
  transform: translateY(-1rem);
}

.unhovered {
  opacity: .5;
}

.paragraph-active {
  opacity: 1 !important;
  transform: translateY(0) !important;
}

.mobile {
  display: inline-block;
}

@media only screen and (width <= 75em) {
  .mobile {
    visibility: none !important;
    display: none !important;
  }
}

.margin-big {
  margin-top: 11rem;
}

.margin-r-s {
  margin-right: 1rem;
}

.margin-l-s {
  margin-left: 1rem;
}

.margin-top-s {
  margin-top: 3rem;
}

.sticky {
  transform: translateY(-3.5rem);
}

@media only screen and (width <= 75em) {
  .sticky {
    transform: none;
  }
}

.margin {
  margin: 2rem;
}

.margin-bottom {
  margin-bottom: 2rem;
}

.icon-size {
  font-size: 2rem;
}

.imagea {
  object-fit: cover;
  z-index: -1;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.hidden-2 {
  visibility: hidden !important;
  display: none !important;
}

.overlay {
  -webkit-backdrop-filter: blur(4px) !important;
  backdrop-filter: blur(4px) !important;
  z-index: 100 !important;
  background-color: #00000080 !important;
  width: 100% !important;
  height: 100% !important;
  transition: all .5s !important;
  position: fixed !important;
  top: 0 !important;
  left: 0 !important;
}

.overlay-img {
  visibility: visible;
  z-index: 101;
  width: 120rem;
  display: block;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.exist {
  cursor: pointer;
  z-index: 300;
  font-size: 8rem;
  position: fixed;
  top: 10%;
  right: 2%;
}

.spinner {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.spinning {
  text-align: center;
  margin: 5rem auto;
  font-size: 10rem;
  animation: 1s linear infinite spin;
}

.spinsmall {
  margin-top: -2rem;
  position: absolute;
  left: 50%;
  font-size: 2rem !important;
  bottom: 0 !important;
}

.flex {
  justify-content: space-evenly;
  align-items: center;
  gap: 54rem;
  display: flex;
}

.header {
  z-index: 2000;
  background-color: #fff;
  background-color: var(--main-color-2);
  border-bottom: 3px solid var(--color-primary-dark);
  justify-content: space-between;
  align-items: center;
  height: 7rem;
  padding: 2rem;
  font-size: 1.3rem;
  transition: all .3s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.nav__list {
  flex: 0 0 40%;
  justify-content: flex-end;
  align-items: center;
  gap: 6rem;
  margin-left: 7rem;
  list-style: none;
  display: flex;
}

@media only screen and (width <= 75em) {
  .nav__list {
    display: none;
  }
}

.nav__link {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  outline: none;
  font-weight: 500;
  text-decoration: none;
  transition: all .3s;
  position: relative;
}

.nav__link:hover > span {
  width: 100%;
}

.line {
  background: linear-gradient(to right, var(--color-primary-light), var(--color-primary-dark));
  opacity: 1;
  border-radius: 1rem;
  width: 0;
  height: .2rem;
  transition: all .5s;
  position: absolute;
  bottom: -3px;
  left: 0;
}

.active-nav {
  width: 100%;
}

.landing {
  z-index: 1;
  max-width: 100%;
  min-height: 100vh;
  margin-top: 7rem;
  position: relative;
  overflow: hidden;
}

.landing-2 {
  padding: 5rem 0 3rem;
  overflow: hidden;
}

.landing-3 {
  padding: 0 1rem 15rem;
  overflow: hidden;
}

.landing:after {
  content: "";
  background-color: var(--color-Grey-dark-1);
  z-index: -1;
  transform-origin: center;
  border-radius: 0;
  width: 150%;
  height: 100%;
  position: absolute;
  bottom: 0;
  left: 60%;
  transform: translateY(90%)translateX(-45%)skewX(20deg);
}

.news {
  background-image: linear-gradient(to top right, #5297f894, #84bfffa4), url("psbackground.1156593b.webp");
  background-position: 0;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 10rem 2rem;
}

.search_results {
  z-index: 1;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  gap: 2rem;
  min-height: 50vh;
  margin-top: 2.5rem;
  margin-left: 2rem;
  display: flex;
  position: relative;
}

.search_results-2 {
  justify-content: center;
  align-items: center;
  gap: 5rem;
  min-height: 50vh;
  margin: 0;
  padding: 5rem 1rem;
}

.search__title {
  margin-left: 2rem;
  text-transform: none !important;
  font-size: 2.25rem !important;
}

.result-bar {
  color: var(--color-Grey-light-4);
  z-index: 2;
  justify-content: flex-start;
  align-items: center;
  gap: 3rem;
  margin-bottom: 3rem;
  display: flex;
  position: relative;
}

.game_results {
  flex-wrap: wrap;
  align-items: center;
  gap: 2rem;
  display: flex;
}

.games-genre {
  z-index: 2;
  gap: 2.5rem;
  padding: 5rem 2rem;
  display: flex;
  position: relative;
}

.search__genres {
  background-color: var(--main-color-2);
  z-index: -1;
  flex-direction: column;
  flex: 0 0 15%;
  display: flex;
  position: relative;
}

.genre__title {
  text-transform: uppercase;
  color: #fff;
  z-index: 2;
  cursor: pointer;
  margin: 2rem;
  padding: 1rem 1.2rem;
  font-size: 1.8rem;
  transition: all .3s;
  position: relative;
}

.genre__title:after {
  content: "";
  background-color: var(--color-primary);
  z-index: -1;
  width: 0;
  height: 100%;
  transition: all .3s;
  position: absolute;
  top: 0;
  left: 0;
}

.genre__title:hover:after {
  width: 100%;
}

.active-button {
  background-color: var(--color-primary);
  box-shadow: var(--neon-light);
  transition: all .3s;
}

#platforms {
  margin-top: 8rem;
}

.platforms {
  scroll-behavior: smooth;
  scroll-snap-type: x mandatory;
  background-image: linear-gradient(to top right, #6b79d1, #6e82d6, #728bda, #7794de, #7d9de2, #75a9eb, #6fb4f2, #69bff8, #52cffe, #41b6ff, #46a3fa, #5f81fb);
  justify-content: flex-start;
  align-items: center;
  gap: 3rem;
  margin-bottom: 4rem;
  padding: 6rem 1rem;
  font-family: Changa, sans-serif;
  display: flex;
  position: relative;
  overflow: hidden;
}

.game-plat {
  cursor: pointer;
  margin: .5rem;
  background-color: var(--main-color) !important;
  width: 44rem !important;
}

.game__background-plat {
  width: 44rem !important;
}

.plat__results {
  position: relative;
}

