.footer-cta {
  border-bottom: 1px solid #373636;
}

.single-cta i {
  color: var(--color-primary);
  float: left;
  margin-top: 8px;
  font-size: 30px;
}

.cta-text {
  padding-left: 15px;
  display: inline-block;
}

.cta-text h4 {
  color: #fff;
  margin-bottom: 2px;
  font-size: 20px;
  font-weight: 600;
}

.cta-text span {
  color: #757575;
  font-size: 15px;
}

.footer-content {
  z-index: 2;
  position: relative;
}

.footer-pattern img {
  background-position: 100% 100%;
  background-size: cover;
  height: 330px;
  position: absolute;
  top: 0;
  left: 0;
}

.footer-logo {
  margin-bottom: 30px;
}

.footer-logo img {
  max-width: 200px;
}

.footer-text p {
  color: #7e7e7e;
  margin-bottom: 14px;
  font-size: 14px;
  line-height: 28px;
}

.footer-social-icon span {
  color: #fff;
  font-family: Poppins, sans-serif;
  font-size: 20px;
  font-weight: 700;
  display: block;
}

.footer-social-icon a {
  color: #fff;
  margin-right: 15px;
  font-size: 16px;
}

.footer-social-icon i {
  text-align: center;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  line-height: 38px;
}

.facebook-bg {
  background: #3b5998;
}

.twitter-bg {
  background: #55acee;
}

.google-bg {
  background: #dd4b39;
}

.footer-widget-heading h3 {
  color: #fff;
  margin-bottom: 40px;
  font-size: 20px;
  font-weight: 600;
  position: relative;
}

.footer-widget-heading h3:before {
  content: "";
  background: var(--color-primary);
  width: 50px;
  height: 2px;
  position: absolute;
  bottom: -15px;
  left: 0;
}

.footer-widget ul li {
  float: left;
  width: 50%;
  margin-bottom: 12px;
  display: inline-block;
}

.footer-widget ul li a:hover {
  color: var(--color-primary);
}

.footer-widget ul li a {
  color: #878787;
  text-transform: capitalize;
}

.subscribe-form {
  position: relative;
  overflow: hidden;
}

.subscribe-form input {
  color: #fff;
  background: #2e2e2e;
  border: 1px solid #2e2e2e;
  width: 100%;
  padding: 14px 28px;
}

